<template>
  <div class="checkbox">
    <fx-card title="基础用法">
      <fx-checkbox v-model="baseCheckbox">备选项</fx-checkbox>
      <p>当前选择: {{ baseCheckbox }}</p>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="禁用状态">
      <fx-checkbox v-model="disabledCheckbox1" disabled>备选项</fx-checkbox>
      <fx-checkbox v-model="disabledCheckbox2" disabled>备选项</fx-checkbox>
      <template #hide>
        <pre v-highlight="disabled"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="多选框组">
      <fx-checkbox-group v-model="checkboxGroup">
        <fx-checkbox label="备选项 A"></fx-checkbox>
        <fx-checkbox label="备选项 B" disabled></fx-checkbox>
        <fx-checkbox label="备选项 C"></fx-checkbox>
        <fx-checkbox label="备选项 D"></fx-checkbox>
        <fx-checkbox label="备选项 E"></fx-checkbox>
      </fx-checkbox-group>
      <br>
      {{ checkboxGroup }}
      <template #hide>
        <pre v-highlight="checkboxGroups"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="数量限制 min=1 max=3">
      <fx-checkbox-group v-model="numberCheckbox" :min="1" :max="3">
        <fx-checkbox label="备选项 A"></fx-checkbox>
        <fx-checkbox label="备选项 B"></fx-checkbox>
        <fx-checkbox label="备选项 C"></fx-checkbox>
        <fx-checkbox label="备选项 D"></fx-checkbox>
        <fx-checkbox label="备选项 E"></fx-checkbox>
      </fx-checkbox-group>
      <br>
      {{ numberCheckbox }}
      <template #hide>
        <pre v-highlight="number"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="按钮样式">
      <ul class="list">
        <li>
          <fx-checkbox-group v-model="buttonCheckbox" size="large">
            <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
            <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
          </fx-checkbox-group>
        </li>
        <li>
          <fx-checkbox-group v-model="buttonCheckbox" size="middle">
            <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
            <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
          </fx-checkbox-group>
        </li>
        <li>
          <fx-checkbox-group v-model="buttonCheckbox" size="default">
            <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
            <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
          </fx-checkbox-group>
        </li>
        <li>
          <fx-checkbox-group v-model="buttonCheckbox" size="mini">
            <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
            <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
            <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
          </fx-checkbox-group>
        </li>
      </ul>
      {{ buttonCheckbox }}
      <template #hide>
        <pre v-highlight="button"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from 'vue'

export default defineComponent({
  name: 'FCheckbox',
  setup () {
    const baseCheckbox = ref<boolean>(true)
    const disabledCheckbox1 = ref<boolean>(true)
    const disabledCheckbox2 = ref<boolean>(false)
    const checkboxGroup = reactive<string[]>(['备选项 B'])
    const numberCheckbox = reactive<string[]>(['备选项 B'])
    const buttonCheckbox = reactive<string[]>(['备选项 A'])

    const base = `<fx-checkbox v-model="baseCheckbox">备选项</fx-checkbox>
{{ baseCheckbox }}`
    const disabled = `<fx-checkbox v-model="disabledCheckbox1" disabled>备选项</fx-checkbox>
<fx-checkbox v-model="disabledCheckbox2" disabled>备选项</fx-checkbox>`
    const checkboxGroups = `<fx-checkbox-group v-model="checkboxGroup">
  <fx-checkbox label="备选项 A"></fx-checkbox>
  <fx-checkbox label="备选项 B" disabled></fx-checkbox>
  <fx-checkbox label="备选项 C"></fx-checkbox>
  <fx-checkbox label="备选项 D"></fx-checkbox>
  <fx-checkbox label="备选项 E"></fx-checkbox>
</fx-checkbox-group>
{{ checkboxGroup }}`
    const number = `<fx-checkbox-group v-model="numberCheckbox" :min="1" :max="3">
  <fx-checkbox label="备选项 A"></fx-checkbox>
  <fx-checkbox label="备选项 B" disabled></fx-checkbox>
  <fx-checkbox label="备选项 C"></fx-checkbox>
  <fx-checkbox label="备选项 D"></fx-checkbox>
  <fx-checkbox label="备选项 E"></fx-checkbox>
</fx-checkbox-group>
{{ numberCheckbox }}`
    const button = `<ul class="list">
  <li>
    <fx-checkbox-group v-model="buttonCheckbox" size="large">
      <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
      <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
    </fx-checkbox-group>
  </li>
  <li>
    <fx-checkbox-group v-model="buttonCheckbox" size="middle">
      <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
      <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
    </fx-checkbox-group>
  </li>
  <li>
    <fx-checkbox-group v-model="buttonCheckbox" size="default">
      <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
      <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
    </fx-checkbox-group>
  </li>
  <li>
    <fx-checkbox-group v-model="buttonCheckbox" size="mini">
      <fx-checkbox-button label="备选项 A"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 B"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 C" disabled></fx-checkbox-button>
      <fx-checkbox-button label="备选项 D"></fx-checkbox-button>
      <fx-checkbox-button label="备选项 E"></fx-checkbox-button>
    </fx-checkbox-group>
  </li>
</ul>
{{ buttonCheckbox }}`

    return {
      baseCheckbox,
      disabledCheckbox1,
      disabledCheckbox2,
      checkboxGroup,
      numberCheckbox,
      buttonCheckbox,
      base,
      disabled,
      checkboxGroups,
      button,
      number
    }
  }
})
</script>

<style scoped lang="scss">
.checkbox {
  .list {
    > li {
      margin-bottom: 10px;
    }
  }
}
</style>
